<template>
  <div class="venue-container">
    <div class="title" @click="handleGoBack">
      <div class="name">
        {{ venue.name }}
      </div>
      <div class="right">
        <van-icon name="arrow-left" />
        <div>返回</div>
      </div>
    </div>
    <div class="detail-content">
      <div class="img-container">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(image, index) in venue.images" :key="index">
            <img v-lazy="image" style="height: 150px;margin-top:8px;" />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="venue-info">
        <van-cell :value="venue.info" />
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Lazyload } from 'vant'
Vue.use(Lazyload)
export default {
  props: {
    venue: {
      type: Object,
      require: true,
      default: () => {}
    }
  },
  methods: {
    handleGoBack() {
      this.$emit('handleGoBack')
    }
  }
}
</script>

<style>
.venue-container {
  padding: 8px;
}
.title {
  color: #1890ff;
  display: flex;
  height: 24px;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.title :hover {
  cursor: pointer;
}
.right {
  display: flex;
  height: 24px;
  flex-direction: row;
  align-items: center;
}
.detail-content{
  overflow-y: auto;
}
.name {
}
</style>
