<template>
  <div class="home-container">
    <base-map @handleMapInit="handleMapInit" />
    <div class="content">
      <div v-if="showVenueDetail">
        <venue-detail :venue="venue" @handleGoBack="handleGoBack" />
      </div>
      <div v-else class="search">
        <div class="search-container">
          <van-search
            v-model="venueName"
            placeholder="请输入场馆名称"
            input-align="center"
            @input="handleSearch"
          />
        </div>
        <div class="list-container">
          <venue-list :list="list" @goToVenue="handleGoToVenue" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseMap from '@/components/map'
import venuesData from '@/assets/data/venues.json'
import VenueList from '@/view/venueList'
import VenueDetail from '@/view/venueDetail'
export default {
  components: {
    BaseMap,
    VenueList,
    VenueDetail
  },
  data() {
    return {
      showVenueDetail: false,
      map: {},
      venueName: null,
      list: venuesData,
      venue: {}
    }
  },
  methods: {
    handleMapInit(map) {
      this.map = map
      this.handleDrawVenues()
    },
    handleDrawVenues() {
      const AMap = window.AMap
      venuesData.forEach((item) => {
        const gps = [item.lgtd, item.lttd]
        AMap.convertFrom(gps, 'baidu', (status, result) => {
          if (result.info === 'ok') {
            const lnglats = result.locations // Array.<LngLat>
            const zoomStyleMapping1 = {
              4: 0,
              5: 0,
              6: 0,
              7: 0,
              8: 0,
              9: 0,
              10: 0,
              11: 0,
              12: 0,
              13: 1,
              14: 1,
              15: 1,
              16: 1,
              17: 1,
              18: 1,
              19: 1,
              20: 1
            }
            const marker = new AMap.ElasticMarker({
              position: lnglats[0],
              zooms: [11, 20],
              styles: [
                {
                  icon: {
                    // img: require(`../../assets/img/${imgName}.png`),
                    img: require(`@/assets/img/${item.symbol}`),
                    size: [80, 30], //可见区域的大小
                    ancher: [0, 0], //锚点
                    fitZoom: 12, //最合适的级别
                    scaleFactor: 2, //地图放大一级的缩放比例系数
                    maxScale: 2, //最大放大比例
                    minScale: 1 //最小放大比例
                  },
                  label: {
                    content: item.name,
                    offset: [0, 0],
                    position: 'TM',
                    minZoom: 12,
                    maxZoomL: 16
                  }
                },
                {
                  icon: {
                    // img: require(`../../assets/img/${imgName}.png`),
                    img: require(`@/assets/img/${item.symbol}`),
                    size: [80, 30], //可见区域的大小
                    ancher: [0, 0], //锚点
                    fitZoom: 20, //最合适的级别
                    scaleFactor: 2, //地图放大一级的缩放比例系数
                    maxScale: 2, //最大放大比例
                    minScale: 1 //最小放大比例
                  },
                  label: {
                    content: item.name,
                    offset: [-35, 0],
                    position: 'TM',
                    minZoom: 12,
                    maxZoomL: 16
                  }
                }
              ],
              zoomStyleMapping: zoomStyleMapping1
            })
            marker.on('click', () => {
              const flag = this.showVenueDetail
              if (!flag) {
                this.venue = item
                this.showVenueDetail = true
              }
            })
            this.map.add(marker)
          }
        })
      })
    },
    handleSearch(value) {
      const data = venuesData.concat()
      const res = data.filter((item) => {
        return item.name.indexOf(value) !== -1
      })
      this.list = res
    },
    handleGoToVenue(item) {
      this.showVenueDetail = true
      const images = []
      if (item.picture) {
        item.picture.forEach((pic) => {
          const img = require(`../../assets/img/${pic}`)
          images.push(img)
        })
      }
      item.images = images
      this.venue = item
      const map = this.map
      const zoom = 17
      const gps = [item.lgtd, item.lttd]
      const AMap = window.AMap
      AMap.convertFrom(gps, 'baidu', (status, result) => {
        if (result.info === 'ok' || status) {
          map.setZoomAndCenter(zoom, result.locations[0])
        }
      })
    },
    handleGoBack() {
      // this.map.setZoomAndCenter(12, [108.94, 34.34])
      this.showVenueDetail = false
    }
  }
}
</script>

<style>
.home-container {
  height: 100vh;
}
.bt {
  position: absolute;
  bottom: 20px;
}
.amap-marker-label {
  width: 120px;
  font-size: 12px;
  line-height: 18px;
  text-align: center;
  word-wrap: break-word;
  word-break: break-all;
  height: 18px;
  color: green;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
.marker-label {
  border: none;
}
.amap-icon img {
  position: relative;
}
.content {
  position: absolute;
  bottom: 0px;
  height: 35%;
  width: 100%;
  background-color: #fff;
  z-index: 999;
  display: flex;
  flex-direction: column;
}
.search {
  position: absolute;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #fff;
  z-index: 999;
  display: flex;
  flex-direction: column;
}
.search-container {
  height: 54px;
}
.list-container {
  position: relative;
  overflow-y: auto;
}
</style>
